import * as DelegationManager from './DelegationManager';
import * as DeleGatorCore from './DeleGatorCore';
import * as EIP712 from './EIP712';
import * as EntryPoint from './EntryPoint';
import * as HybridDeleGator from './HybridDeleGator';
import * as MultiSigDeleGator from './MultiSigDeleGator';
import * as Ownable2Step from './Ownable2Step';
import * as Pausable from './Pausable';
import * as SimpleFactory from './SimpleFactory';
import * as utils from './utils';

// NOTE: Unfortunately due to the nested namespaces we have to manually merge them here to ensure type generation works.

const encode: typeof HybridDeleGator.encode &
  typeof MultiSigDeleGator.encode &
  typeof DelegationManager.encode &
  typeof DeleGatorCore.encode &
  typeof Ownable2Step.encode &
  typeof Pausable.encode &
  typeof SimpleFactory.encode = {
  ...HybridDeleGator.encode,
  ...MultiSigDeleGator.encode,
  ...DelegationManager.encode,
  ...DeleGatorCore.encode,
  ...Ownable2Step.encode,
  ...Pausable.encode,
  ...SimpleFactory.encode,
};

const execute: typeof DelegationManager.execute &
  typeof Ownable2Step.execute &
  typeof Pausable.execute &
  typeof SimpleFactory.execute &
  typeof DeleGatorCore.execute = {
  ...DelegationManager.execute,
  ...Ownable2Step.execute,
  ...Pausable.execute,
  ...SimpleFactory.execute,
  ...DeleGatorCore.execute,
};

const read: typeof HybridDeleGator.read &
  typeof MultiSigDeleGator.read &
  typeof DeleGatorCore.read &
  typeof HybridDeleGator.read &
  typeof DelegationManager.read &
  typeof EntryPoint.read &
  typeof EIP712.read &
  typeof Ownable2Step.read &
  typeof Pausable.read &
  typeof SimpleFactory.read = {
  ...HybridDeleGator.read,
  ...MultiSigDeleGator.read,
  ...DeleGatorCore.read,
  ...DelegationManager.read,
  ...EntryPoint.read,
  ...EIP712.read,
  ...Ownable2Step.read,
  ...Pausable.read,
  ...SimpleFactory.read,
};

const simulate: typeof HybridDeleGator.simulate &
  typeof MultiSigDeleGator.simulate &
  typeof DelegationManager.simulate &
  typeof DeleGatorCore.simulate &
  typeof Ownable2Step.simulate &
  typeof Pausable.simulate &
  typeof SimpleFactory.simulate = {
  ...HybridDeleGator.simulate,
  ...MultiSigDeleGator.simulate,
  ...DelegationManager.simulate,
  ...DeleGatorCore.simulate,
  ...Ownable2Step.simulate,
  ...Pausable.simulate,
  ...SimpleFactory.simulate,
};

const constants: {
  DelegationManager: typeof DelegationManager.constants;
  HybridDeleGator: typeof HybridDeleGator.constants;
  MultiSigDeleGator: typeof MultiSigDeleGator.constants;
} = {
  DelegationManager: DelegationManager.constants,
  HybridDeleGator: HybridDeleGator.constants,
  MultiSigDeleGator: MultiSigDeleGator.constants,
};

/**
 * Delegation Framework
 *
 * A suite of utility methods for interacting with the Delegation Framework contracts.
 */
export const DelegationFramework = {
  encode,
  execute,
  read,
  simulate,
  constants,
  utils,
};
