export type JSONRPCError = { message: string; code: number; data?: any };

export class RpcError extends Error {
  public readonly code: number;

  public readonly data?: object;

  public static INVALID_PARAMETERS = -32602;

  public static INTERNAL_ERROR = -32603;

  public static METHOD_NOT_SUPPORTED = -32601;

  // implementation-defined server-error
  public static CONNECTION_CLOSED = -32001;

  public static from(error: RpcError | Error | string): RpcError {
    if (error instanceof RpcError) {
      return error;
    }

    const errorMessage = error instanceof Error ? error.message : error;

    return new RpcError(errorMessage, RpcError.INTERNAL_ERROR);
  }

  public toJSONRPCError(): JSONRPCError {
    const { message, code, data } = this;

    return {
      message,
      code,
      data,
    };
  }

  constructor(message: string, code: number, data?: object) {
    super(message);

    // https://www.typescriptlang.org/docs/handbook/2/classes.html#inheriting-built-in-types
    Object.setPrototypeOf(this, RpcError.prototype);

    this.code = code;
    this.data = data;
  }
}
