import type { DeleGatorEnvironment, Caveat } from '..';
import { toHex } from 'viem';

export const id = 'id';

/**
 * Builds a caveat struct for the IdEnforcer.
 *
 * @param environment - The DeleGator environment.
 * @param id - The id to be enforced.
 * @returns The Caveat.
 * @throws Error if the provided id is not a number, not an integer, or is > 32 bytes.
 */
export const idBuilder = (
  environment: DeleGatorEnvironment,
  id: number,
): Caveat => {
  if (typeof id !== 'number') {
    throw new Error('Invalid id: must be a number');
  }

  if (!Number.isInteger(id)) {
    throw new Error('Invalid id: must be an integer');
  }

  if (id >= 2 ** 32) {
    throw new Error('Invalid id: must be 32 bytes or fewer in length');
  }

  const terms = toHex(id, { size: 32 });

  const {
    caveatEnforcers: { IdEnforcer },
  } = environment;

  return {
    enforcer: IdEnforcer!,
    terms,
    args: '0x',
  };
};
